	module.exports = {"data":{"post":{"html":"<p>I won't use the word 'might' anymore. All three of us will drive down to Amsterdam next weekend. I can't remember the last pleasure trip we took. Before Mama's pregnancy, I'm sure, so it's been at least 2 years. (Except, of course, for our road trip to  <a href=\"http://shanson.edtthispage.com/2000/10/21\">Odessa</a> while in Minnesota last fall. But that wasn't an overnight.)</p>\n<p>I was last in Amsterdam in 1985 on our honeymoon of sorts. We were poor students, and couldn't afford a hotel, only a boat hostel which turned out to be owned by some cult. </p>\n<p>In August we'll go to Sylt, on the North Sea coast, for a week. Mama's employer owns an an apartment for employee use. Sylt has the reputation of being overrun by millionaire snobs in the summer, but the millionaires are pretty easy to avoid, if you're careful.</p>","fields":{"title":"Vacation Plans","date":"17 May 2001","url":"https://papascott.de/2001-05-17-vacation-plans/"}}},"pathContext":{"slug":"/2001-05-17-vacation-plans/","prev":{"fields":{"title":"Road Trip","date":"16 May 2001","slug":"/2001-05-16-road-trip/"}},"next":{"fields":{"title":"The Mug Game","date":"19 May 2001","slug":"/2001-05-19-the-mug-game/"}}}}

